import { HttpClient, HttpEvent } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { VERITAS_API_PROXY_PREFIX_URL } from '../../constants/veritas-api.constant';
import {
  AnswerDetailResponseInterface,
  QuestionnaireInterface
} from '../../interfaces/questionnaire.interface';
import { Attachment } from '../../models/attachment.model';

@Injectable({
  providedIn: 'root'
})
export class DashBoardSurveyControllerService {
  private readonly url = `${this.veritasApiProxyPrefixUrl}/question-survey`;

  constructor(
    private httpClient: HttpClient,
    @Inject(VERITAS_API_PROXY_PREFIX_URL)
    private veritasApiProxyPrefixUrl: string
  ) {}

  /**
   * GET List Tasks Data
   */
  public getQuestionContainerViewPages(
    recordId: string
  ): Observable<QuestionnaireInterface> {
    return this.httpClient
      .get(`${this.url}/${recordId}`)
      .pipe(map(this.mapGetQuestionContainerViewPages));
  }

  public postAttachments$(
    recordId: string,
    questionNodeId: string,
    files: File[]
  ): Observable<HttpEvent<Attachment[]>> {
    const formData = new FormData();
    files.forEach(file => formData.append('attachmentFiles', file));

    return this.httpClient.post<any>(
      `${this.url}/${recordId}/question/${questionNodeId}/attachments`,
      formData,
      {
        reportProgress: true,
        observe: 'events'
      }
    );
  }

  public deleteAttachment$(
    recordId: string,
    questionNodeId: string,
    attachmentId: string
  ): Observable<Attachment[]> {
    return this.httpClient.delete<Attachment[]>(
      `${
        this.url
      }/${recordId}/question/${questionNodeId}/attachments/${attachmentId}`
    );
  }

  private mapGetQuestionContainerViewPages(questionnaire) {
    return questionnaire;
  }

  public postQuestionDetailResponses(
    answers: AnswerDetailResponseInterface[],
    submitAction: boolean,
    recordId: string
  ): Observable<any> {
    // Assemble post data
    const answersData = {
      answers: answers,
      submitAction: submitAction
    };
    return this.httpClient.post(
      `/api/question-survey/${recordId}`,
      answersData
    );
  }
}
