export enum RespondentTypeEnum {
  Reviewer = 'REVIEWER',
  RemediationTaskReviewer = 'REMEDIATION_TASK_REVIEWER',
  PrivacyTeam = 'PRIVACY_TEAM',
  BusinessOwner = 'BUSINESS_OWNER',
  VendorContact = 'VENDOR_CONTACT',
  HumanResourcesTeam = 'HUMAN_RESOURCES_TEAM',
  ItSecurityTeam = 'IT_SECURITY_TEAM',
  Other = 'OTHER'
}

export namespace RespondentTypeEnum {
  export function getDisplayText(value: RespondentTypeEnum): string {
    let result = '';
    switch (value) {
      case RespondentTypeEnum.Reviewer:
        result = 'Reviewer';
        break;

      case RespondentTypeEnum.RemediationTaskReviewer:
        result = 'Remediation Task Reviewer';
        break;

      case RespondentTypeEnum.PrivacyTeam:
        result = 'Privacy Team';
        break;

      case RespondentTypeEnum.BusinessOwner:
        result = 'Business Owner';
        break;

      case RespondentTypeEnum.VendorContact:
        result = 'Vendor Contact';
        break;

      case RespondentTypeEnum.HumanResourcesTeam:
        result = 'Human Resources Team';
        break;

      case RespondentTypeEnum.ItSecurityTeam:
        result = 'IT/Security Team';
        break;

      case RespondentTypeEnum.Other:
        result = 'Other';
        break;

      default:
        result = value;
        break;
    }

    return result;
  }
}
