import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { VERITAS_API_PROXY_PREFIX_URL } from '../../constants/veritas-api.constant';
import { CustomEncoder } from '../../custom-encoder';
import { PageInterface } from '../../interfaces/page.interface';
import { Assignee } from '../../models/assignee.model';

@Injectable({
  providedIn: 'root'
})
export class UserControllerService {
  private readonly url = `${this.veritasApiProxyPrefixUrl}/users`;
  private readonly maxRows = 10;

  constructor(
    private httpClient: HttpClient,
    @Inject(VERITAS_API_PROXY_PREFIX_URL)
    private veritasApiProxyPrefixUrl: string
  ) {}

  public getUsers$(
    page: number,
    search: string = null
  ): Observable<PageInterface<Assignee>> {
    let params = new HttpParams({ encoder: new CustomEncoder() })
      .append('page', page.toString())
      .append('size', this.maxRows.toString());

    if (search) {
      params = params.append('search', search);
    }

    return this.httpClient.get<PageInterface<Assignee>>(this.url, {
      params
    });
  }
}
