export enum TaskStatusEnum {
  Open = 'OPEN',
  InProgress = 'IN_PROGRESS',
  InReview = 'IN_REVIEW',
  Closed = 'CLOSED',
  ActionRequired = 'ACTION_REQUIRED'
}

export namespace TaskStatusEnum {
  export function getDisplayText(value: TaskStatusEnum): string {
    let result = '';
    switch (value) {
      case TaskStatusEnum.Open:
        result = 'Open';
        break;

      case TaskStatusEnum.InProgress:
        result = 'In Progress';
        break;

      case TaskStatusEnum.InReview:
        result = 'In Review';
        break;

      case TaskStatusEnum.Closed:
        result = 'Closed';
        break;

      case TaskStatusEnum.ActionRequired:
        result = 'Action Required';
        break;
    }

    return result;
  }
}
