import { HttpClient, HttpParams } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { PAGE_OFFSET_FOR_SERVER } from '../../constants/page.constant';
import { VERITAS_API_PROXY_PREFIX_URL } from '../../constants/veritas-api.constant';
import { CommentEntityTypeEnum } from '../../enums/comment-entity-type.enum';
import { PageInterface } from '../../interfaces/page.interface';
import { AnswerDetailResponseInterface } from '../../interfaces/questionnaire.interface';
import { Comment } from '../../models/comment.model';

@Injectable({
  providedIn: 'root'
})
export class DashBoardCommentControllerService {
  private readonly url = `${this.veritasApiProxyPrefixUrl}/comment`;

  constructor(
    private httpClient: HttpClient,
    @Inject(VERITAS_API_PROXY_PREFIX_URL)
    private veritasApiProxyPrefixUrl: string
  ) {}

  public getComments$(
    entityId: string,
    page: number = 1,
    maxRows: number = 20
  ): Observable<PageInterface<Comment>> {
    const params = new HttpParams()
      .append('page', (page + PAGE_OFFSET_FOR_SERVER).toString())
      .append('size', maxRows.toString());

    return this.httpClient
      .get<PageInterface<Comment>>(`${this.url}/${entityId}`, {
        params
      })
      .pipe(
        map(response => {
          response.content.forEach((comment: Comment) => {
            comment.isEditing = false;
          });
          return response;
        })
      );
  }

  public createComment$(
    entityId: string,
    entityType: CommentEntityTypeEnum,
    message: string
  ): Observable<Comment> {
    const data = {
      entityId,
      entityType,
      messageText: message
    };

    return this.httpClient.post<Comment>(`${this.url}/${entityId}`, data);
  }

  public updateComment$(
    entityId: string,
    entityType: CommentEntityTypeEnum,
    commentId: string,
    message: string
  ): Observable<Comment> {
    const data = {
      entityId,
      entityType,
      messageText: message,
      id: commentId
    };

    return this.httpClient.post<Comment>(
      `${this.url}/${entityId}/${commentId}`,
      data
    );
  }

  public deleteComment$(
    entityId: string,
    commentId: string
  ): Observable<Comment> {
    return this.httpClient.delete<Comment>(
      `${this.url}/${entityId}/${commentId}`
    );
  }

  public getQuestionContainerViewPages(taskId: string): Observable<any> {
    return this.httpClient
      .get(`/api/question-survey/${taskId}`)
      .pipe(map(this.mapGetQuestionContainerViewPages));
  }

  public getAttachmentDownloadUrl$(attachmentId: string): Observable<any> {
    return this.httpClient.get(`${this.url}/attachments/${attachmentId}/url`);
  }

  private mapGetQuestionContainerViewPages(questionnaire) {
    return questionnaire;
  }

  public postQuestionDetailResponses(
    answers: AnswerDetailResponseInterface[],
    submitAction: boolean,
    taskId: string
  ): Observable<any> {
    // Assemble post data
    const answersData = {
      answers: answers,
      submitAction: submitAction
    };
    return this.httpClient.post(`/api/question-survey/${taskId}`, answersData);
  }
}
